import React from 'react';
import { useState, useEffect } from 'react'
import { useParams } from 'react-router';
import { Link } from 'react-router-dom';
import supabase from '../../api/Supabase';
import { Tag, Divider, message, Breadcrumb } from 'antd';
import { ShoppingCartOutlined } from '@ant-design/icons';
import { useCartContext } from "../../context/Cart";
import SocialShare from '../../components/SocialShare';
import formatNumberCommas from '../../utilities/NumberWithCommas';
import isEmptyObj from '../../utilities/isEmptyObj';

interface Props {
  detail: any
}

const ProductPage:React.FC<Props> = (props:Props) => {
    const {increaseItemQuantity} = useCartContext();
    let {id} = useParams();
    const [data, setData] = useState<any>([]);
    const [isData, setIsData] = useState(false);
    const [activeImg, setActiveImage] = useState('');
    const {detail} = props;

    async function getData() {
      if(isEmptyObj(detail) == true){ //get data from api if detail from props is empty (page refresh)
        let {data} = await supabase.from('product').select('*').eq('id', id);
        setData(data?.[0]);
      }
      else{ //get data from detail in props when user click product item
        setData(detail);
      }
      setIsData(true);
    }

    useEffect(() => {
      getData();
    }, []);

    if(isData){
      const {images} = data;
      return (
          <div className='flex flex-col justify-between lg:flex-row gap-16 lg:items-center'>
              <div className='flex flex-col gap-6 lg:w-2/4'>
                  <div className="h-[300px]">
                    <img src={(activeImg) ? activeImg : data.images[0].url} alt="" className='w-full h-full aspect-square object-contain rounded-xl'/>
                  </div>
                  <div className='flex flex-row justify-center space-x-2 h-24'>
                      {
                        images.map((obj:any, index:number) => {
                          return <img src={obj.url} key={'image' + index} alt="" className='w-24 h-24 rounded-md cursor-pointer' onClick={() => setActiveImage(obj.url)}/>
                        })
                      }
                  </div>
              </div>
              <div className='flex flex-col gap-4 lg:w-2/4'>
                  <Breadcrumb
                    items={[
                      {
                        title: <Link to="/">Trang chủ</Link>,
                      },
                      {
                        title: <Link to="/store">Cửa hàng</Link>,
                      },
                      {
                        title: <span>{data.name}</span>,
                      },
                    ]}
                  />
                  <div>
                      <span className=' text-violet-600 font-semibold'></span>
                      <h1 className='text-3xl'>{data.name}</h1>
                  </div>
                  {
                    (data.sale_percent) ? (
                      <h6 className='text-2xl font-semibold space-x-4'>
                        <del className="text-[#CCC] text-[14px]">{formatNumberCommas(data.price)} &#8363;</del>
                        <span>{formatNumberCommas(data.sale_price)} &#8363;</span>
                        <Tag color="#F50">-{data.sale_percent}%</Tag>
                      </h6>
                    ):
                    (
                      <h6 className='text-2xl font-semibold'>
                      {formatNumberCommas(data.price)} &#8363;
                      </h6>
                    )
                  }
                  <div className='flex flex-row items-center gap-12'>
                      <button
                        onClick={() => {increaseItemQuantity(data); message.success('Đã thêm vào giỏ hàng!');}}
                        className="text-[18px] border-[2px] border-[solid] border-[#000] px-10 py-2 hover:bg-[#000] hover:text-[#FFF]"
                      >
                        <ShoppingCartOutlined /> Thêm vào giỏ
                      </button>
                  </div>
                  <p className='text-gray-700'>
                    Mô tả: &nbsp;
                    {data.description}
                  </p>
                  <Divider />
                  <SocialShare />
              </div>
          </div>
      )
    }
    else{
      return <div></div>
    }
}

export default ProductPage
